<template>
    <div class="two-columns adaptive-full">
        <div class="left-column">
            <div class="white-shadow-block mob-none">
                <div class="filter-block-title">{{translation.select_tower}}</div>
                <div class="svg-all-plan" v-html="korpus">

                </div>
            </div>
            <form class="filters search_form" id="search_form" action="" method="post">

                <div class="white-shadow-block">
                    <div class="filter-block-title">{{translation.immovable_type}}</div>
                    <div class="inline-btn">
                        <div v-bind:class="[ 'flats'  == choosed  ?  'active-btn' : '', 'btn  form-check-div' ]"
                             value="flats" @click="immovableType('flats')">{{translation.flats}}
                        </div>
                        <div v-bind:class="[ 'apartment'  == choosed  ?  'active-btn' : '', 'btn  form-check-div' ]"
                             value="apartment" @click="immovableType('apartment')">{{translation.apartment}}
                        </div>

                        <div v-bind:class="[ 'comoros'  == choosed  ?  'active-btn' : '', 'btn  form-check-div' ]"
                             value="comoros" @click="immovableType('comoros')">{{translation.comoros}}
                        </div>
                        <div v-bind:class="[ 'commerce'  == choosed  ?  'active-btn' : '', 'btn  form-check-div' ]"
                             value="commerce" @click="immovableType('commerce')">{{translation.commercial_immovable}}
                        </div>
                        <div v-bind:class="[ 'parking'  == choosed  ?  'active-btn' : '', 'btn  form-check-div' ]"
                             value="parking" @click="immovableType('parking')"> {{translation.parking}}
                        </div>

                        <div class="right-btn btn  form-check-div"
                             value="all" @click="immovableType('all')">{{translation.all_floors}}
                        </div>
                    </div>
                </div>
                <!--ToDo показать фильтр если нерухомость-->
                <div class="white-shadow-block" id="commerce_block" v-show="commercialsOpen">
                    <div class="filter-block-title">{{translation.commercial_immovable}}</div>
                    <div class="inline-btn">
                        <div class="btn form-check-div-2"
                             value="office" @click="immovableCommercial('office')">{{translation.offices}}
                        </div>
                        <div class="btn  form-check-div-2"
                             value="retail" @click="immovableCommercial('retail')">{{translation.retail}}
                        </div>
                        <div class="btn  form-check-div-2"
                             value="restaurants" @click="immovableCommercial('restaurants')">{{translation.restaurants}}
                        </div>
                    </div>
                </div>
                <!--ToDo показать фильтр квартир если квартиры-->
                <div class="white-shadow-block" id="flats_block" v-show="flatsOpen">
                    <div class="filter-block-title">{{translation.flats}}</div>
                    <div class="checkboxes-block flexibal-between">
                        <div class="checkboxes-block-left flexibal-between">
                            <div class="block-check">
                                <input id="block-check-1" class="block-check-input" name="rooms_quantity[]"
                                       type="checkbox"
                                       value="1" @change="roomsQuantity('1')">
                                <label class="form-check-label" for="block-check-1">1</label>
                            </div>
                            <div class="block-check">
                                <input id="block-check-2" class="block-check-input" name="rooms_quantity[]"
                                       type="checkbox"
                                       value="2" @change="roomsQuantity('2')">
                                <label class="form-check-label " for="block-check-2">2</label>
                            </div>
                            <div class="block-check">
                                <input id="block-check-3" class="block-check-input" name="rooms_quantity[]"
                                       type="checkbox"
                                       value="3" @change="roomsQuantity('3')">
                                <label class="form-check-label" for="block-check-3">3</label>
                            </div>
                            <div class="block-check">
                                <input id="block-check-11" class="block-check-input" name="rooms_quantity[]"
                                       type="checkbox"
                                       value="1+" @change="roomsQuantity('1+')">
                                <label class="form-check-label" for="block-check-11">1+</label>
                            </div>
                            <div class="block-check">
                                <input id="block-check-12" class="block-check-input" name="rooms_quantity[]"
                                       type="checkbox"
                                       value="2+" @change="roomsQuantity('2+')">
                                <label class="form-check-label" for="block-check-12">2+</label>
                            </div>
                            <div class="block-check">
                                <input id="block-check-13" class="block-check-input" name="rooms_quantity[]"
                                       type="checkbox"
                                       value="3+" @change="roomsQuantity('3+')">
                                <label class="form-check-label" for="block-check-13">3+</label>
                            </div>
                        </div>
                        <div class="checkboxes-block-right flexibal-between">
                            <div class="block-check block-check-big">
                                <input id="block-check-33" class="block-check-input" name="rooms_quantity[]"
                                       type="checkbox"
                                       value="penthouse" @change="roomsQuantity('penthouse')">
                                <label class="form-check-label"
                                       for="block-check-33">{{translation.two_level}}</label>
