                    && (immovableComm ? immovableComm == flat.immovable_commerce_type : true)
                    && (roomsQuan.length ? roomsQuan.indexOf(flat.rooms_quantity) > -1 : true)
                    && ploshaMaxSet >= flat.total_square && ploshaMinSet <= flat.total_square
