                                                                            <path class="st0" d="M17.7,391.7c-2.7,0-5.4-1-7.5-2.7c-5-4.1-5.6-11.5-1.5-16.5l69.2-83.2l98.7,44.4c5.9,2.6,8.5,9.6,5.9,15.5
