</template>

<script>
    import VueSlider from 'vue-slider-component'

    export default {
        name: 'app',
        components: {
            VueSlider,
        },
        data() {
            return {
                showen: {},
                toShow: 12,
                translation: window.loc_obj.translation,
                korpus: window.loc_obj.korpusa,
                asset: window.loc_obj.asset,
                lang: document.getElementsByTagName("html")[0].getAttribute("lang"),
                flats: window.loc_obj.flats.original[0],
                roomsQuan: [],
                immovableComm: null,
                immovableTyp: null,
                floorMin: 0,
                korpusa: window.loc_obj.korpuses,
                all_flats_array: window.loc_obj.all_flats_array,
                floorMax: window.loc_obj.default_max_floor,
                floorMinSet: 0,
                floorMaxSet: window.loc_obj.default_max_floor,
                ploshaMin: 0,
                ploshaMinSet: 0,
                sliderPagin: [],
                ploshaMax: 0,
                ploshaMaxSet: 0,
                showSlider: false,
                ploshaModel: [this.ploshaMinSet, this.ploshaMaxSet],
                floorModel: [this.floorMinSet, this.floorMaxSet],
                corpus: null,
                slidesFlors: [],
                choosed: false,
                commercialsOpen: false,
                flatsOpen: false,
                slider_props: {
                    min: 0,
                    value: [0, 0],
                    max: 0,
                    show: true,
                    realTime: true,
                    tooltip: "always",
                },
            }
        },
        mounted() {
            this.getMaxParams();
            this.svgBinds();
        },
        methods: {
            immovableType: function (n) {
                this.resetFilters();
                if (this.choosed != n) {
                    if (n == 'all') {
                        this.immovableTyp = null;
                    } else {
                        this.immovableTyp = n;
                    }
                    this.flatsOpen = false;
                    this.commercialsOpen = false;

                    if (n == 'apartment' || n == 'flats') {
                        this.flatsOpen = true;
                    } else if (n == 'commerce') {
                        this.commercialsOpen = true;
                    }
                } else {
                    return

                }
                this.getMaxParams();
                this.showen.count = 0;
                this.choosed = n;
            },
            immovableCommercial: function (n) {
                this.showen.count = 0;
                this.immovableComm = n;
                this.getMaxParams();

            },

            roomsQuantity: function (n) {
                this.showen.count = 0;
                if (this.roomsQuan.indexOf(n) > -1) {
                    this.roomsQuan.splice(this.roomsQuan.indexOf(n), 1);
                } else {
                    this.roomsQuan.push(n);
                }
                this.getMaxParams();

            },
            resetFilters: function (n) {
                this.immovableTyp = null;
                this.immovableComm = null;
                this.roomsQuan = [];
                this.toShow = 12;
                this.getMaxParams();
                this.resetFloors();
                this.resetPlosha();
                this.showSlider = false;
                this.showen.count = 0;
                this.choosed = null;

                let cheks = document.querySelectorAll('.block-check');
                for (let i = 0; i < cheks.length; i++) {
                    cheks[i].querySelector('input').checked = false
                }
                // let btns = document.querySelectorAll('.form-check-div');
                // for (let i = 0; i < btns.length; i++) {
                //     btns[i].classList.remove('active-btn')
                // }
                let btns2 = document.querySelectorAll('.form-check-div-2');
                for (let i = 0; i < btns2.length; i++) {
                    btns2[i].classList.remove('active-btn')
                }
                if(n == 'all') {
                    let korpuses = document.querySelectorAll('.korpus_hover');
                    for (let i = 0; i < korpuses.length; i++) {
                        korpuses[i].classList.remove('korpus_present');
                    }
                    this.setCorpus(null)
                }
            },
            increseShowen: function () {
                if (this.showen.count == undefined) {
                    this.showen.count = 1;
                } else {
                    this.showen.count += 1;
                }
            },

            showMore: function () {
                this.toShow += 12;
            },

            getMaxParams: function () {
                this.ploshaMax = 0;
                this.floorMaxSet = 0;

                this.floorMinSet = 100
                for (let flat in this.flats) {
                    if (
                        (this.immovableTyp ? this.immovableTyp == this.flats[flat].immovable_type : true)
                        && (this.immovableComm ? this.immovableComm == this.flats[flat].immovable_commerce_type : true)
                        && (this.roomsQuan.length ? this.roomsQuan.indexOf(this.flats[flat].rooms_quantity) > -1 : true)
                        && (this.corpus ? this.corpus == this.flats[flat].tower_id : true)
                    ) {
                        if (this.ploshaMax < +this.flats[flat].total_square) {
                            this.ploshaMax = Math.floor(+this.flats[flat].total_square);

                        }
                        if (this.floorMaxSet < +this.flats[flat].floor) {
                            this.floorMaxSet = Math.floor(+this.flats[flat].floor);
                        }
                        if (this.floorMinSet > +this.flats[flat].floor) {
                            this.floorMinSet = Math.floor(+this.flats[flat].floor);
                        }
                    }
                }

                if (this.floorMaxSet == 0 && this.floorMinSet == 100) {
                    this.floorMinSet = 0;
                    this.floorMaxSet == 0;
                }

                this.floorMax = this.floorMaxSet
                this.floorMin = this.floorMinSet
                this.ploshaMaxSet = this.ploshaMax;
                this.ploshaMinSet = 0;
                this.ploshaModel = [this.ploshaMinSet, this.ploshaMaxSet];
                this.floorModel = [this.floorMinSet, this.floorMaxSet];
                this.showen.count = 0;

            },

            setValuesPl: function () {
                this.showen.count = 0;
                let val = this.$refs.slider.getValue();
                this.ploshaMaxSet = val[1];
                this.ploshaMinSet = val[0];
            },

            setValuesFl: function () {
                this.showen.count = 0;
                let val = this.$refs.slider2.getValue();
                if (val[1] == val[0] && this.corpus) {
                    this.showSlider = true;
                    this.floorMaxSet = val[1];
                    this.floorMinSet = val[0];
                    this.parseSlider();
                } else {
                    this.showSlider = false;
                    this.floorMaxSet = val[1];
                    this.floorMinSet = val[0];
                }

            },

            sliderFloorInit: function () {
                this.sliderPagin = [];
                for (let i = -2, j = 0; i < 3; i++, j++) {
                    let c = this.floorMaxSet - this.floorMin + i
                    c >= this.slidesFlors.length ?  c = i - 1 : '';
                    c = c < 0 ? this.slidesFlors[this.slidesFlors.length + c] :   this.slidesFlors[c];
                    this.sliderPagin.push(c)
                }
            },

            toSlide: function (act) {
                this.showen.count = 0;
                if(act == '-1') {
                    this.floorMaxSet =  this.floorMaxSet + ~~act < this.floorMin  ? this.floorMax : this.floorMaxSet - 1;
                }else if(act == '+1') {
                    this.floorMaxSet = this.floorMaxSet +  ~~act > this.floorMax ? this.floorMin : this.floorMaxSet + 1;
                }else {
                    this.floorMaxSet = ~~act;
                }
                this.floorMinSet = this.floorMaxSet
                this.floorModel = [this.floorMinSet, this.floorMaxSet];
                this.sliderFloorInit()
            },

            parseSlider: function () {
                this.slidesFlors = [];

                for (let item in this.korpusa[this.corpus]) {
                    if(this.korpusa[this.corpus][item].floor >= this.floorMin && this.korpusa[this.corpus][item].floor <= this.floorMax)
                        this.slidesFlors.push(this.korpusa[this.corpus][item]);
                }
                this.slidesFlors.sort((a, b) => {
                    return a.floor - b.floor;
                });
                this.sliderFloorInit();
            },

            svgBinds: function () {
                let _this = this;
                let korpuses = document.querySelectorAll('.korpus_hover');

                for (let i = 0; i < korpuses.length; i++) {
                    korpuses[i].addEventListener('click', function () {
                        let korpusData = this.getAttribute('data-build');
                        if (this.classList.contains('korpus_present')) {
                            if (korpusData == 'build_business') {
                                for (let i = 0; i < korpuses.length; i++) {
                                    let korpusDataCurr = korpuses[i].getAttribute('data-build');
                                    if (korpusDataCurr == 'build_business') {
                                        korpuses[i].classList.remove('korpus_present');
                                    }
                                }
                            }else {
                                this.classList.remove('korpus_present');
                            }
                            _this.setCorpus(null)
                        } else if (korpusData != 'build_a' && korpusData != 'build_d' &&
                            korpusData != 'build_appart' && korpusData != 'build_business') {
                            for (let i = 0; i < korpuses.length; i++) {
                                korpuses[i].classList.remove('korpus_present');
                            }
                            this.classList.add('korpus_present');
                            _this.setCorpus(this.getAttribute('data-tower-id'))
                        } else if (korpusData == 'build_business') {
                            for (let i = 0; i < korpuses.length; i++) {
                                let korpusDataCurr = korpuses[i].getAttribute('data-build');
                                if (korpusDataCurr == 'build_business') {
                                    korpuses[i].classList.add('korpus_present');
                                } else {
                                    korpuses[i].classList.remove('korpus_present');
                                }
                                _this.setCorpus(this.getAttribute('data-tower-id'))
                            }
                        }
                    })
                }
            },

            setCorpus: function (n) {
                this.corpus = n
                this.resetFilters()
            },

            resetFloors: function () {
                this.floorMaxSet = this.floorMax;
                this.floorMinSet = 0;
                this.floorModel = [this.floorMinSet, this.floorMaxSet];
                this.showen.count = 0;

            },

            resetPlosha: function () {
                this.getMaxParams()
            },

            goFlat:function (e) {
                let nodes = Array.prototype.slice.call( document.querySelectorAll('.flore-image.js-coise-flat.active .svg-house svg path'))
                let clickTargetIndex = nodes.indexOf( e.target )
                this.all_flats_array
                let link = this.all_flats_array[this.corpus][this.floorMaxSet][clickTargetIndex].url;
                location.href = link;
            }
        }

    }
</script>

<style scoped>
    #ellipse-housing {
        enable-background: new 0 0 1304 723.4;
    }

    .ellipse {
        fill: none;
        stroke: #c0bab3;
        stroke-width: 9;
        stroke-miterlimit: 10;
    }
</style>