import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        GET_OBJ: {},
        COUNT: 0
    },
    getters: {
        GET_OBJ: state => {
            return state.globalObj.router
        },
        COUNT: state => {
            return state.COUNT
        },
    },
    mutations: {
        GET_OBJ: (state, items) => {
            state.GET_OBJ = items;
        },
        COUNT: (state, count) => {
            state.COUNT = count;
        },
    },
    actions: {
        GET_OBJ: async (context) => {
            // axios.get('http://intergal.j2landing.com/get-all-flats-page.js')
            axios.get('/get-all-flats-page.js')
                .then(function (response) {
                    context.commit('GET_OBJ', JSON.parse(response));
                })
        },
        COUNT: async (context, count) => {
            context.commit('COUNT', count);
        }
    }
})
